:root {
  --color-white: #fff;
  --color-black: #000;
  --color-orange: #ff6b08;
  --color-red: #ff001b;
  --color-background: #e5e5e5;
  --color-footer-background: #f7f7f7;
  --font-roboto: "Roboto", sans-serif;
  --mobile-breakpoint: 320px;
  --tablet-breakpoint: 768px;
  --desktop-breakpoint: 1280px;
}

* {
  box-sizing: border-box;
  font-family: var(--font-roboto);
  scroll-behavior: smooth;
  outline: 0;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body, h1, h2, h3, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.visually-hidden {
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

