particle {
  pointer-events: none;
  opacity: 0;
  border-radius: 50%;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  z-index: 1;
  background-color: #0006;
  width: 100%;
  height: 100%;
  padding-top: 50px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal.show {
  animation: .5s ease-in-out fadeIn;
  display: block;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.modal-content {
  background-color: #fefefe;
  border: 1px solid #888;
