 .header{
  background-image: url(../image/main-background-mobile.jpg);
  min-height: 230px;
  width: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;

}
@media (min-device-pixel-ratio: 2),
(min-resolution: 2dppx),
(min-resolution: 192dpi) {
  .header {
    background: url(../image/main-background-mobile@2x.jpg);

    background-size: cover;
  }
}
.headerSection {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.navigation {
  display: flex;
  color: #fff;
  width: 320px;
  justify-content:space-between;
  align-items: center;
  margin: 0 auto;
  padding: 40px 20px 0 20px;
}


.logo{
  color: #fff;
  stroke:#fff;

  transition: color .25s cubic-bezier(.4, 0, .2, 1), stroke .25s cubic-bezier(.4, 0, .2, 1);;

}
.textLogo {
  margin-left: 10px;
  font-size: 30px;
  font-weight: 500;
  line-height: 1.17;
  display: none;
}


.navi{
  display: flex;
  gap:40px;
}


.link{
  color: #fff;
  fill:#fff;
    padding-top: 2px;
    padding-bottom: 2px;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.17;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.naviItem{
  position: relative;
}


.naviItemDash::after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #ff001b;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
}


.logo:hover,
.logo:focus,
.link:hover,
.link:focus {
  color: var(--color-orange, orange);
  fill: var(--color-orange, orange);
  stroke:var(--color-orange, orange);
}


.form{
text-align: center;
}
.search-form{
    margin-top: 60px;
  width: 280px;
    position: relative;
    /* max-width: 320px; */
    display: inline-block;
    justify-content: center;
    
    
}

.searchQuery{
  width: 100%;
  height: 20px;
  caret-color: #fff;
  background: 0 0;
  border: none;
  border-bottom-width: medium;
  border-bottom-style: none;
  border-bottom-color: currentcolor;
  border-bottom: .5px solid #fff;
  font-weight: 400;
  font-size: 14px;
  color: #fff;
  padding-right: 15px;
}

.iconBtn{
position: absolute;
top: 0;
right: 0%;
height: 100%;

background: transparent;
border: none;
cursor: pointer;
padding: 0;

}

.svgSearch{
  width:10px;
  height: 10px;
  fill: none;
  stroke: #fff;

}
.errorText{
  margin: 20px auto;
  max-width: 280px;
  text-align: left;
  color:var(--color-red,red);
    font-weight: 400;
      font-size: 14px;

}
.hiddenVisibility{
  visibility: hidden;
  /* Ukrycie elementu zachowując jego przestrzeń w układzie strony */
}

  @media screen and (min-width: 768px) {
  .header {
    background-image: url(../image/main-background-tablet.jpg);
    min-height: 216px;


  }

  @media (min-device-pixel-ratio: 2),
  (min-resolution: 2dppx),
  (min-resolution: 192dpi) {
    .header {
      background:
        url(../image/main-background-tablet@2x.jpg);

        background-size: cover;
    }
  }

  .navigation {
    width: 768px;
    padding: 45px 31px 0 31px;
  }

  .textLogo {
    display: inline-block;
  }

  .search-form {
    margin-top: 40px;
    width: 336px;
  }

.errorText {
  max-width: 393px;
}

}

@media screen and (min-width: 1280px) {
.header {
    background-image: url(../image/main-background-desktop.jpg);


  }

  @media (min-device-pixel-ratio: 2),
  (min-resolution: 2dppx),
  (min-resolution: 192dpi) {
    .header {
      background:
        url(../image/main-background-desktop@2x.jpg);

      background-size: cover;
    }
  }
  .navigation {
    width: 1280px;
    padding: 45px 31px 0 31px;
  } 

    .search-form {
      margin-top: 40px;
      width: 393px;
    }


}
